<script setup lang="ts">
import { PropType } from 'vue'
import { Therapist } from '../types'

const avatarColor = (therapistName: string) => {
    const colors = ['primary', '#FFD43A', '#ADFF00', '#262824', 'danger']
    const index = therapistName.charCodeAt(0) % colors.length
    return colors[index]
}

defineProps({
    therapist: {
        type: Object as PropType<Therapist>,
        required: true,
    },
    size: {
        type: String,
        default: 'medium',
    },
})

const isUrl = (avatar: string) => {
    if (!avatar) return false
    return avatar.startsWith('http') || avatar.startsWith('blob:')
}
</script>

<template>
    <VaAvatar
        :size="size"
        :src="isUrl(therapist.coverImageUrl) ? therapist.coverImageUrl : ''"
        :fallback-text="therapist.name[0]"
        :color="avatarColor(therapist.name)"
    />
</template>
