<script setup lang="ts">
import { PropType, computed, ref, watch } from 'vue'
import { useForm, useToast } from 'vuestic-ui'
import { Therapist, CreateTherapistRequest, UpdateTherapistRequest } from '../types'
import TherapistAvatar from '../widgets/TherapistAvatar.vue'
import { validators } from '../../../services/utils'
import { useTherapists } from '../composables/useTherapists'
const { uploadImages } = useTherapists()
const props = defineProps({
    therapist: {
        type: Object as PropType<Therapist | null>,
        default: null,
    },
    saveButtonLabel: {
        type: String,
        default: 'Save',
    },
})

const defaultNewTherapist: Therapist = {
    id: -1,
    number: 0,
    name: '',
    coverImage: '',
    images: [],
    height: 0,
    weight: 0,
    birthYear: 0,
    description: '',
    technicalLevel: '',
    removal: '',
    serviceMedal: '',
    note: '',
    recommendRank: '',
    paymentNote: '',
    sortOrder: 0,
    isActive: true,
    createdAt: '',
    updatedAt: '',
    deletedAt: '',
    coverImageUrl: '',
    imageUrls: [],
}

const newTherapist = ref<Therapist>({ ...defaultNewTherapist })

const isFormHasUnsavedChanges = computed(() => {
    return Object.keys(newTherapist.value).some((key) => {
        if (key === 'avatar' || key === 'images') {
            return false
        }

        return (
            newTherapist.value[key as keyof Therapist] !==
            (props.therapist ?? defaultNewTherapist)?.[key as keyof Therapist]
        )
    })
})

defineExpose({
    isFormHasUnsavedChanges,
})

watch(
    () => props.therapist,
    () => {
        if (!props.therapist) {
            return
        }

        newTherapist.value = {
            ...props.therapist,
        }
    },
    { immediate: true },
)

const avatar = ref<File>()
const originalImages = ref<File[]>([])

const makeAvatarBlobUrl = (avatar: File) => {
    return URL.createObjectURL(avatar)
}

const makeImageBlobUrl = (image: File) => {
    return URL.createObjectURL(image)
}

const removeExistingImage = (index: number) => {
    if (newTherapist.value.imageUrls) {
        newTherapist.value.imageUrls.splice(index, 1)
    }
    if (newTherapist.value.images) {
        newTherapist.value.images.splice(index, 1)
    }
}

watch(avatar, (newAvatar) => {
    newTherapist.value.coverImageUrl = newAvatar ? makeAvatarBlobUrl(newAvatar) : ''
})

const form = useForm('add-therapist-form')

const emit = defineEmits<{
    close: []
    save: [therapist: CreateTherapistRequest | UpdateTherapistRequest]
}>()

const onSave = async () => {
    // 首先驗證表單，如果驗證失敗就直接返回
    if (!form.validate()) {
        return
    }

    if (!avatar.value && !newTherapist.value.coverImage) {
        // 如果沒有上傳封面圖片，且無現有封面圖片，則不可送出
        useToast().init({ message: '請上傳大頭照', position: 'top-right', color: 'danger' })
        return
    }
    if (originalImages.value.length === 0 && newTherapist.value.images.length === 0) {
        // 如果沒有上傳相片集，且無現有相片集，則不可送出
        useToast().init({ message: '請至少上傳一張相片', position: 'top-right', color: 'danger' })
        return
    }

    if (avatar.value) {
        await uploadImages([avatar.value]).then((response: string[]) => {
            newTherapist.value.coverImage = response[0]
        })
    }
    if (originalImages.value.length > 0) {
        await uploadImages(originalImages.value).then((response: string[]) => {
            // 增量式新增圖片，不覆蓋既有的圖片
            if (!newTherapist.value.images) {
                newTherapist.value.images = []
            }
            newTherapist.value.images.push(...response)
        })
    }

    // 判斷是新增還是編輯
    if (props.therapist) {
        // 編輯模式 - 使用 UpdateTherapistRequest
        const therapistData: UpdateTherapistRequest = {
            id: props.therapist.id,
            number: Number(newTherapist.value.number) || 0,
            name: newTherapist.value.name,
            coverImage: newTherapist.value.coverImage,
            images: newTherapist.value.images,
            height: newTherapist.value.height ? Number(newTherapist.value.height) : 0,
            weight: newTherapist.value.weight ? Number(newTherapist.value.weight) : 0,
            birthYear: newTherapist.value.birthYear ? Number(newTherapist.value.birthYear) : 0,
            description: newTherapist.value.description,
            technicalLevel: newTherapist.value.technicalLevel,
            removal: newTherapist.value.removal,
            serviceMedal: newTherapist.value.serviceMedal,
            note: newTherapist.value.note,
            recommendRank: newTherapist.value.recommendRank,
            paymentNote: newTherapist.value.paymentNote,
            sortOrder: Number(newTherapist.value.sortOrder) || 0,
            isActive: newTherapist.value.isActive,
        }

        emit('save', therapistData)
    } else {
        // 新增模式 - 使用 CreateTherapistRequest
        const therapistData: CreateTherapistRequest = {
            number: Number(newTherapist.value.number) || 0,
            name: newTherapist.value.name,
            coverImage: newTherapist.value.coverImage,
            images: newTherapist.value.images,
            height: newTherapist.value.height ? Number(newTherapist.value.height) : 0,
            weight: newTherapist.value.weight ? Number(newTherapist.value.weight) : 0,
            birthYear: newTherapist.value.birthYear ? Number(newTherapist.value.birthYear) : 0,
            description: newTherapist.value.description,
            technicalLevel: newTherapist.value.technicalLevel,
            removal: newTherapist.value.removal,
            serviceMedal: newTherapist.value.serviceMedal,
            note: newTherapist.value.note,
            recommendRank: newTherapist.value.recommendRank,
            paymentNote: newTherapist.value.paymentNote,
            sortOrder: Number(newTherapist.value.sortOrder) || 0,
            isActive: newTherapist.value.isActive,
        }

        emit('save', therapistData)
    }
}
</script>

<template>
    <VaForm
        v-slot="{ isValid }"
        ref="add-therapist-form"
        class="flex-col justify-start items-start gap-4 inline-flex w-full"
    >
        <VaFileUpload
            v-model="avatar"
            type="single"
            hide-file-list
            class="self-stretch justify-start items-center gap-4 inline-flex"
        >
            <TherapistAvatar :therapist="newTherapist" size="large" />
            <VaButton preset="primary" size="small">Add image</VaButton>
            <VaButton
                v-if="avatar"
                preset="primary"
                color="danger"
                size="small"
                icon="delete"
                class="z-10"
                @click.stop="avatar = undefined"
            />
        </VaFileUpload>
        <div class="self-stretch flex-col justify-start items-start gap-4 flex">
            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput
                    v-model="newTherapist.name"
                    label="名稱"
                    class="w-full sm:w-1/2"
                    :rules="[validators.required]"
                    name="name"
                />
                <VaInput
                    v-model="newTherapist.number"
                    label="號碼"
                    class="w-full sm:w-1/2"
                    :rules="[validators.required]"
                    name="number"
                    type="number"
                />
            </div>
            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput
                    v-model="newTherapist.height"
                    label="身高 (cm)"
                    class="w-full sm:w-1/2"
                    name="height"
                    type="number"
                />
                <VaInput
                    v-model="newTherapist.weight"
                    label="體重 (kg)"
                    class="w-full sm:w-1/2"
                    name="weight"
                    type="number"
                />
            </div>
            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput
                    v-model="newTherapist.birthYear"
                    label="出生年份"
                    class="w-full sm:w-1/2"
                    name="birthYear"
                    type="number"
                />
                <VaInput
                    v-model="newTherapist.technicalLevel"
                    label="技術級別"
                    class="w-full sm:w-1/2"
                    name="technicalLevel"
                />
            </div>
            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput
                    v-model="newTherapist.serviceMedal"
                    label="服務獎章"
                    class="w-full sm:w-1/2"
                    name="serviceMedal"
                />
                <VaInput
                    v-model="newTherapist.recommendRank"
                    label="推薦指數"
                    class="w-full sm:w-1/2"
                    name="recommendRank"
                />
            </div>

            <!-- 複數相片上傳 -->
            <div class="w-full">
                <label class="va-input-label">相片集</label>
                <VaFileUpload
                    v-model="originalImages"
                    type="gallery"
                    file-types="image/*"
                    class="w-full"
                    hide-file-list
                >
                    <VaButton preset="primary" size="small">上傳相片</VaButton>
                </VaFileUpload>

                <!-- 顯示縮圖 -->
                <div
                    v-if="newTherapist.imageUrls?.length > 0 || originalImages.length > 0"
                    class="flex flex-wrap gap-2 mt-4"
                >
                    <!-- 顯示舊有照片縮圖 -->
                    <div
                        v-for="(imageUrl, index) in newTherapist.imageUrls"
                        :key="`existing-${index}`"
                        class="relative w-20 h-20 border border-gray-300 rounded overflow-hidden"
                    >
                        <img :src="imageUrl" :alt="`Existing Photo ${index + 1}`" class="w-full h-full object-cover" />
                        <VaButton
                            preset="primary"
                            color="danger"
                            size="small"
                            icon="delete"
                            class="absolute top-1 right-1 w-6 h-6 min-w-0 p-0"
                            @click="removeExistingImage(index)"
                        />
                    </div>

                    <!-- 顯示新上傳照片縮圖 -->
                    <div
                        v-for="(image, index) in originalImages"
                        :key="`new-${index}`"
                        class="relative w-20 h-20 border border-gray-300 rounded overflow-hidden"
                    >
                        <img
                            :src="makeImageBlobUrl(image)"
                            :alt="`New Photo ${index + 1}`"
                            class="w-full h-full object-cover"
                        />
                        <VaButton
                            preset="primary"
                            color="danger"
                            size="small"
                            icon="delete"
                            class="absolute top-1 right-1 w-6 h-6 min-w-0 p-0"
                            @click="originalImages.splice(index, 1)"
                        />
                    </div>
                </div>
            </div>

            <div class="flex gap-4 w-full">
                <div class="flex items-center w-1/2 mt-4">
                    <VaCheckbox v-model="newTherapist.isActive" label="Active" class="w-full" name="active" />
                </div>
            </div>

            <VaTextarea v-model="newTherapist.description" label="Description" class="w-full" name="description" />
            <VaTextarea v-model="newTherapist.note" label="Notes" class="w-full" name="note" />
            <VaTextarea v-model="newTherapist.paymentNote" label="Payment Note" class="w-full" name="paymentNote" />

            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput
                    v-model="newTherapist.sortOrder"
                    label="Sort Order"
                    class="w-full sm:w-1/2"
                    :rules="[validators.required]"
                    name="sortOrder"
                    type="number"
                />
                <VaInput v-model="newTherapist.removal" label="Removal Status" class="w-full sm:w-1/2" name="removal" />
            </div>
            <div class="flex gap-2 flex-col-reverse items-stretch justify-end w-full sm:flex-row sm:items-center">
                <VaButton preset="secondary" color="secondary" @click="$emit('close')">Cancel</VaButton>
                <VaButton :disabled="!isValid" @click="onSave">{{ saveButtonLabel }}</VaButton>
            </div>
        </div>
    </VaForm>
</template>
