<script setup lang="ts">
import { defineVaDataTableColumns, useModal } from 'vuestic-ui'
import { Therapist } from '../types'
import TherapistAvatar from '../widgets/TherapistAvatar.vue'
import { PropType, computed, toRef } from 'vue'
import { Pagination, Sorting } from '../../../data/pages/therapists'
import { useVModel } from '@vueuse/core'

const columns = defineVaDataTableColumns([
    { label: 'Number', key: 'number', sortable: true },
    { label: 'Name', key: 'name', sortable: true },
    { label: 'Sort Order', key: 'sortOrder', sortable: true },
    { label: ' ', key: 'actions', align: 'right' },
])

const props = defineProps({
    therapists: {
        type: Array as PropType<Therapist[]>,
        required: true,
    },
    loading: { type: Boolean, default: false },
    pagination: { type: Object as PropType<Pagination>, required: true },
    sortBy: { type: String as PropType<Sorting['sortBy']>, required: true },
    sortingOrder: { type: String as PropType<Sorting['sortingOrder']>, required: true },
})

const emit = defineEmits<{
    (event: 'edit-therapist', therapist: Therapist): void
    (event: 'delete-therapist', therapist: Therapist): void
    (event: 'update:sortBy', sortBy: Sorting['sortBy']): void
    (event: 'update:sortingOrder', sortingOrder: Sorting['sortingOrder']): void
}>()

const therapists = toRef(props, 'therapists')
const sortByVModel = useVModel(props, 'sortBy', emit)
const sortingOrderVModel = useVModel(props, 'sortingOrder', emit)

const totalPages = computed(() => Math.ceil(props.pagination.total / props.pagination.perPage))

const { confirm } = useModal()

const onTherapistDelete = async (therapist: Therapist) => {
    const agreed = await confirm({
        title: 'Delete therapist',
        message: `Are you sure you want to delete ${therapist.name}?`,
        okText: 'Delete',
        cancelText: 'Cancel',
        size: 'small',
        maxWidth: '380px',
    })

    if (agreed) {
        emit('delete-therapist', therapist)
    }
}
</script>

<template>
    <VaDataTable
        v-model:sort-by="sortByVModel"
        v-model:sorting-order="sortingOrderVModel"
        :columns="columns"
        :items="therapists"
        :loading="$props.loading"
    >
        <template #cell(number)="{ rowData }">
            <div class="font-semibold">
                {{ rowData.number }}
            </div>
        </template>

        <template #cell(name)="{ rowData }">
            <div class="flex items-center gap-2 max-w-[230px] ellipsis">
                <TherapistAvatar :therapist="rowData as Therapist" size="small" />
                {{ rowData.name }}
            </div>
        </template>

        <template #cell(sortOrder)="{ rowData }">
            <div class="text-center font-mono">
                {{ rowData.sortOrder }}
            </div>
        </template>

        <template #cell(actions)="{ rowData }">
            <div class="flex gap-2 justify-end">
                <VaButton
                    preset="primary"
                    size="small"
                    icon="mso-edit"
                    aria-label="Edit therapist"
                    @click="$emit('edit-therapist', rowData as Therapist)"
                />
                <VaButton
                    preset="primary"
                    size="small"
                    icon="mso-delete"
                    color="danger"
                    aria-label="Delete therapist"
                    @click="onTherapistDelete(rowData as Therapist)"
                />
            </div>
        </template>
    </VaDataTable>

    <div class="flex flex-col-reverse md:flex-row gap-2 justify-between items-center py-2">
        <div>
            <b>{{ $props.pagination.total }} results.</b>
            Results per page
            <VaSelect v-model="$props.pagination.perPage" class="!w-20" :options="[10, 50, 100]" />
        </div>

        <div v-if="totalPages > 1" class="flex">
            <VaButton
                preset="secondary"
                icon="va-arrow-left"
                aria-label="Previous page"
                :disabled="$props.pagination.page === 1"
                @click="$props.pagination.page--"
            />
            <VaButton
                class="mr-2"
                preset="secondary"
                icon="va-arrow-right"
                aria-label="Next page"
                :disabled="$props.pagination.page === totalPages"
                @click="$props.pagination.page++"
            />
            <VaPagination
                v-model="$props.pagination.page"
                buttons-preset="secondary"
                :pages="totalPages"
                :visible-pages="5"
                :boundary-links="false"
                :direction-links="false"
            />
        </div>
    </div>
</template>

<style lang="scss" scoped>
.va-data-table {
    ::v-deep(.va-data-table__table-tr) {
        border-bottom: 1px solid var(--va-background-border);
    }
}
</style>
