import { Ref, ref, watch } from 'vue'
import { Therapist, CreateTherapistRequest, UpdateTherapistRequest } from '../types'
import { watchIgnorable } from '@vueuse/core'
import api from '../../../services/api'

// 類型定義移到這裡，不再依賴 data 層
export type Pagination = {
    page: number
    perPage: number
    total: number
}

export type Sorting = {
    sortBy: keyof Therapist | undefined
    sortingOrder: 'asc' | 'desc' | null
}

export type Filters = {
    isActive: boolean
    search: string
}

const makePaginationRef = () => ref<Pagination>({ page: 1, perPage: 10, total: 0 })
const makeSortingRef = () => ref<Sorting>({ sortBy: 'name', sortingOrder: null })
const makeFiltersRef = () => ref<Partial<Filters>>({ isActive: true, search: '' })

export const useTherapists = (options?: {
    pagination?: Ref<Pagination>
    sorting?: Ref<Sorting>
    filters?: Ref<Partial<Filters>>
}) => {
    const isLoading = ref(false)
    const therapists = ref<Therapist[]>([])

    const { filters = makeFiltersRef(), sorting = makeSortingRef(), pagination = makePaginationRef() } = options || {}

    const fetch = async () => {
        isLoading.value = true
        const { data } = await api.getTherapists({
            // ...unref(filters),
            // ...unref(sorting),
            isActive: filters.value.isActive,
            offset: pagination.value.page * pagination.value.perPage - pagination.value.perPage,
            limit: pagination.value.perPage,
        } as {
            limit: number
            offset: number
        })
        therapists.value = data.data
        console.log(data, 'data')
        ignoreUpdates(() => {
            pagination.value = {
                page: pagination.value.page,
                perPage: pagination.value.perPage,
                total: data.total || data.data.length,
            }
        })

        isLoading.value = false
    }

    const { ignoreUpdates } = watchIgnorable([pagination, sorting], fetch, { deep: true })

    watch(
        filters,
        () => {
            // Reset pagination to first page when filters changed
            pagination.value.page = 1
            fetch()
        },
        { deep: true },
    )

    fetch()

    return {
        isLoading,

        filters,
        sorting,
        pagination,

        therapists,

        fetch,

        async add(therapist: CreateTherapistRequest) {
            isLoading.value = true
            try {
                await api.axiosInstance.post('/plan/dragon-massage/therapist', therapist)
                await fetch()
            } catch (error) {
                console.error('Error adding therapist:', error)
                throw error
            } finally {
                isLoading.value = false
            }
        },

        async update(therapist: UpdateTherapistRequest) {
            isLoading.value = true
            const { id, ...therapistData } = therapist
            await api.axiosInstance.put(`/plan/dragon-massage/therapist/${id}`, therapistData)
            await fetch()
            isLoading.value = false
        },

        async remove(therapist: Therapist) {
            isLoading.value = true
            await api.axiosInstance.delete(`/plan/dragon-massage/therapist/${therapist.id}`)
            await fetch()
            isLoading.value = false
        },

        async uploadImages(images: File[]): Promise<string[]> {
            try {
                const response = await api.uploadTherapistImages(images)
                return response.data.data.imageNames
            } catch (error) {
                console.error('Error uploading images:', error)
                throw error
            }
        },
    }
}
