<script setup lang="ts">
import { ref } from 'vue'
import TherapistsTable from '../therapists/widgets/TherapistsTable.vue'
import EditTherapistForm from '../therapists/widgets/EditTherapistForm.vue'
import { Therapist, CreateTherapistRequest, UpdateTherapistRequest } from '../therapists/types'
import { useTherapists } from '../therapists/composables/useTherapists'
import { useModal, useToast } from 'vuestic-ui'

const doShowEditTherapistModal = ref(false)

const { therapists, isLoading, filters, sorting, pagination, ...therapistsApi } = useTherapists()

const therapistToEdit = ref<Therapist | null>(null)

const showEditTherapistModal = (therapist: Therapist) => {
    therapistToEdit.value = therapist
    doShowEditTherapistModal.value = true
}

const showAddTherapistModal = () => {
    therapistToEdit.value = null
    doShowEditTherapistModal.value = true
}

const { init: notify } = useToast()

const onTherapistSaved = async (therapist: CreateTherapistRequest | UpdateTherapistRequest) => {
    try {
        if ('id' in therapist) {
            // 編輯模式 - therapist 包含 id 屬性
            await therapistsApi.update(therapist as UpdateTherapistRequest)
            notify({
                message: `${therapist.name} has been updated`,
                color: 'success',
            })
        } else {
            // 新增模式 - therapist 沒有 id 屬性
            await therapistsApi.add(therapist as CreateTherapistRequest)
            notify({
                message: `${therapist.name} has been created`,
                color: 'success',
            })
        }
    } catch (error) {
        notify({
            message: 'Operation failed. Please try again.',
            color: 'danger',
        })
    }
}

const onTherapistDelete = async (therapist: Therapist) => {
    await therapistsApi.remove(therapist)
    notify({
        message: `${therapist.name} has been deleted`,
        color: 'success',
    })
}

const editFormRef = ref()

const { confirm } = useModal()

const beforeEditFormModalClose = async (hide: () => unknown) => {
    if (editFormRef.value.isFormHasUnsavedChanges) {
        const agreed = await confirm({
            maxWidth: '380px',
            message: 'Form has unsaved changes. Are you sure you want to close it?',
            size: 'small',
        })
        if (agreed) {
            hide()
        }
    } else {
        hide()
    }
}
</script>

<template>
    <h1 class="page-title">龍筋技師</h1>

    <VaCard>
        <VaCardContent>
            <div class="flex flex-col md:flex-row gap-2 mb-2 justify-between">
                <div class="flex flex-col md:flex-row gap-2 justify-start">
                    <VaButtonToggle
                        v-model="filters.isActive"
                        color="background-element"
                        border-color="background-element"
                        :options="[
                            { label: 'Active', value: true },
                            { label: 'Inactive', value: false },
                        ]"
                    />
                    <VaInput v-model="filters.search" placeholder="Search">
                        <template #prependInner>
                            <VaIcon name="search" color="secondary" size="small" />
                        </template>
                    </VaInput>
                </div>
                <VaButton @click="showAddTherapistModal">Add Therapist</VaButton>
            </div>

            <TherapistsTable
                v-model:sort-by="sorting.sortBy"
                v-model:sorting-order="sorting.sortingOrder"
                :therapists="therapists"
                :loading="isLoading"
                :pagination="pagination"
                @editTherapist="showEditTherapistModal"
                @deleteTherapist="onTherapistDelete"
            />
        </VaCardContent>
    </VaCard>

    <VaModal
        v-slot="{ cancel, ok }"
        v-model="doShowEditTherapistModal"
        size="small"
        mobile-fullscreen
        close-button
        hide-default-actions
        :before-cancel="beforeEditFormModalClose"
    >
        <h1 class="va-h5">{{ therapistToEdit ? 'Edit therapist' : 'Add therapist' }}</h1>
        <EditTherapistForm
            ref="editFormRef"
            :therapist="therapistToEdit"
            :save-button-label="therapistToEdit ? 'Save' : 'Add'"
            @close="cancel"
            @save="
                (therapist: CreateTherapistRequest | UpdateTherapistRequest) => {
                    onTherapistSaved(therapist)
                    ok()
                }
            "
        />
    </VaModal>
</template>
